import React, { Component } from 'react';
import { Route } from 'react-router-dom'
import Home from './home'
import Auth from './Auth'
import './App.css'


import { Animated } from "react-animated-css";
const home = () => <Home />



class App extends Component {

  constructor(props) {
    super(props)
    this.state = {
      user: false,
      name: ""
    }
    this.isLogin = this.isLogin.bind(this)
    this.isLogout = this.isLogout.bind(this)
  }

  isLogin(e) {
    if (e.key === 'Enter') {
      this.setState({
        user: true,
        name: e.target.value
      })
    }
  }

  isLogout() {
    this.setState({
      user: false,
      name: ""

    })

  }

  render() {



    return (
      <div className="my-app">


       



        <div className="container is-fullhd">

          <div className="notification">



           <Animated animationIn="tada" animationOut="fadeOut" isVisible={true}>
          <div>
          <div class="columns">



<div class="column is-three-fifths">
  <h1 class="title">
    <img src="https://image.flaticon.com/icons/svg/138/138292.svg" style={{ width: '50px' }} />
    <strong> @2302</strong>
  </h1>
  <h2 class="subtitle">
    <code>#</code>
  </h2>


</div>



<div className="column is-2 is-gapless">




</div>






<div class="column is-3-pulled-left">

  {this.state.user ? <strong>{"Welcome " + this.state.name}</strong> : <strong>Welcome Guest! </strong>}

</div>









<div class="column is-2 is-gapless">
  {

    this.state.user ?
      <button class="button is-dark" onClick={this.isLogout} >{this.state.user ? "Logout" : "Login"}</button>
      :
      <input class="input is-primary is-rounded" type="text" placeholder="Type your name" onKeyPress={(e) => this.isLogin(e)} />



  }
</div>



</div>
          </div>
        </Animated>



            



          </div>

        </div>
        <div className="App container">
          {
            this.state.user ? <Route path="/" component={home} /> : <Route path="/" component={Auth} />
          }



        </div>
      </div>
    );
  }
}

export default App;



